<template>
    <div class="card">
        <div class="content-top">
            <div class="left">
                <user-card v-bind:info="info"  v-bind:isCreateVip="isCreateVip"></user-card>
            </div>
            <div class="card-info" v-if="isCreateVip === 1">
                <!-- 激活后 获取信息 -->
                <div class="part-left" v-if="isHasCard === 1">
                    <p class="tit">{{ $L('我的信息') }}</p>
                    <el-form :model="getcardForm" label-width="80px" label-position="left"  class="info-msg">
                        <el-form-item v-for="(item,index) in getcardForm" :key="index" :label="item.name">
                            <el-input size="small" v-model="item.value" type="item.type" disabled/>
                        </el-form-item>
                    </el-form>
                </div>
                <div class="part-left" v-else>
                    <div v-if="openCard === 1">
                        <p class="tit">{{ $L('我的信息') }}</p>
                        <el-form size="small" :model="getcardForm"  label-width="80px" label-position="left"  class="info-msg">
                            <el-form-item v-for="(item,index) in getcardForm" :key="index" :label="formListLang(item.name)">
                                <el-select v-if="item.type === 'radio'" v-model="info[index]">
                                    <el-option v-for="(i,k) in item.value" :key="k" :label="i" :value="i">{{ i }}</el-option>
                                </el-select>
                                <el-date-picker v-else-if="item.type === 'date'"
                                                v-model="info[index]"
                                                type="date"
                                                value-format="yyyy-MM-dd"
                                                :placeholder="$L('请选择生日日期')"
                                                @change="timeFunc">
                                </el-date-picker>
                                <el-input v-else-if="item.name !== $L('手机号')" v-model="info[index]" value="item.user_value" :placeholder="$L('请输入') +formListLang(item.name)"/>
                                <el-input v-else v-model="info[index]" class="input" type="text" :placeholder="$L('请输入手机号')"/>
                            </el-form-item>
                            <el-form-item :label="$L('验证码')" v-if="authCode == 1" class="code">
                                <el-input v-model="code" class="code" type="text" :style="{ width:'100px' }"/>

                                <el-button v-on:click="getCodeFunc" v-show="show" class="get-code" type="primary">{{$L(' 获取验证码')}}</el-button>
                                <el-button v-show="!show" class="get-code" type="primary">{{ count }}</el-button>


                            </el-form-item>
                            <el-form-item class="btn-submit">
                                <el-button class="btn" type="primary" @click="submitFunc">{{ $L('立即激活') }}</el-button>
                            </el-form-item>
                        </el-form>

                    </div>
                    <div  class="right-tips mainFontColor"  v-if="openCard === 0">
                        <p class="text">{{ $L('联系商家开启会员卡！') }}</p>
                    </div>
                    <div  class="right-tips mainFontColor"  v-if="getCard === 0">
                        <p class="text">{{ priceGap == 0 ? $L('消费即赠送会员卡！'): $L('消费满')+priceGap +$L('赠送会员卡！') }}</p>
                    </div>
                </div>
                <div class="part-right">
                    <p class="tit">{{ $L('会员卡详情') }}</p>
                    <ul class="preview-text">
                        <li class="item-li">
                            <span class="item-span">{{ $L('享受折扣') }}</span>
                            <p class="item-p">{{ privilege.discount }}{{ $L('折') }}</p>
                        </li>
                        <li class="item-li">
                            <span class="item-span">{{ $L('特权说明') }}</span>
                            <p class="item-p">
                                <i v-if=" privilege.initBonus !== 0">{{ $L('激活成功即赠送') }}{{ privilege.initBonus }}{{ $L('积分') }}<br></i>
                                <i v-if=" privilege.costMoney !== 0">
                                    {{ $L('每消费') }}{{ privilege.costMoney }}{{ currencyCode }}，{{ $L('赠送') }}{{ privilege.incBonus }}{{ $L('积分') }}<br></i>
                                <i v-if=" privilege.maxBonus !== 0">{{ $L('单次赠送上限') }}{{ privilege.maxBonus }}{{ $L('积分') }}<br></i>
                                <i v-if=" privilege.costBonus !== 0">{{ $L('每使用') }}{{ privilege.costBonus }}{{ $L('积分') }}，{{ $L('抵扣') }}{{ privilege.reduceMoney }}{{ currencyCode }}<br></i>
                                <i v-if=" privilege.maxReduceBonus !== 0">{{ $L('订单满') }}{{ privilege.leastMoney }}{{ currencyCode }}{{ $L('可用积分抵扣 ，单笔使用上限') }}{{ privilege.maxReduceBonus }}{{ $L('积分') }}<br></i>
                                <i v-if=" privilege.discount !== 0">{{ $L('用卡可享受') }}{{ privilege.discount }}{{ $L('折优惠') }}<br></i>
                                <i v-if=" privilege.explain !== 0">{{ privilege.explain }}</i>
                            </p>
                        </li>
                        <li class="item-li">
                            <span class="item-span">{{ $L('有效日期') }}</span>
                            <p class="item-p" v-if="privilege.dateType == 1">{{ $L('永久有效') }}</p>
                            <p class="item-p" v-else>{{ privilege.beginTime +  $L('至')  + privilege.endTime}}</p>
                        </li>
                        <li class="item-li">
                            <span class="item-span">{{ $L('等级规则') }}</span>
                            <p class="item-p">
                                <i v-if=" privilege.costPayPoints !== 0">{{ $L('每消费') }}{{ privilege.costPayPoints }}{{ currencyCode }}{{ $L('赠送') }}
                                    {{ privilege.increaseRankPoints }}{{ $L('经验值') }}<br>
                                </i>
                                <i v-if=" privilege.maxRankPoints !== 0">{{ $L('单笔经验赠送上限为') }}{{ privilege.maxRankPoints }}<br></i>
                                <i v-if=" privilege.initRankPoints !== 0">{{ $L('首次激活卡赠送') }}{{ privilege.initRankPoints }}{{ $L('经验值') }}</i>
                            </p>
                        </li>
                    </ul>
                </div>
            </div>

        </div>
        <div class="content-middle" v-if="info && info.hasOwnProperty('nowEXP') && info.hasOwnProperty('nexEXP')">
            <span class="title">{{ $L('经验记录') }}</span>
            <i class="bbx-font bbx-icon-huiyuan1"></i>
            <span>{{ isHasCard == 0 ?  '&nbsp;' : info.groupName}}</span>
            <el-progress :show-text="false" :percentage="info.nowEXP<=0 ? 0 : info.nowEXP/Number(info.nowEXP+info.nexEXP)*100" color="mainBgColor" ></el-progress>
            <span class="tips">{{ $L('经验值') }}{{ info.nowEXP }}
                <i v-if="info.isAssign === 1">

                </i>
                <i v-else>
                    <i v-if="info.nexEXP === 0">
                        {{ $L('已达到最高级') }}
                    </i>
                    <i v-else-if="info.nexEXP !== 0 && isHasCard !== 0">
                        ，{{ $L('距离下一级还差') }}{{ info.nexEXP }}{{ $L('经验值') }}
                    </i>

                </i>
            </span>
        </div>
        <div class="table-head">
            <el-button type="default" class="left" size="small" @click="delMsgFunc()" :disabled="tableData && tableData.hasOwnProperty('list') && tableData.list.length <= 0">{{ $L('选中删除') }}</el-button>
            <div class="right">
                {{ $L('时间') }}：
                <el-date-picker
                        style="width: 350px;"
                        v-model="SearchTimeZone"
                        type="datetimerange"
                        align="right"
                        size="small"
                        :start-placeholder="$L('开始日期')"
                        :end-placeholder="$L('结束日期')"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        :default-time="['00:00:00', '23:59:59']"
                        @change="SearchTimeFunc">
                </el-date-picker>
            </div>
        </div>
        <div class="table" v-loading="loading">
            <div class="content" v-if="tableData && tableData.hasOwnProperty('list')">
                <el-table
                        :data="tableData.list"
                        :empty-text="$L('暂无数据')"
                        style="width: 100%" class="table"
                        :header-cell-style="{background:'#f0f1f5',color:'#666'}"
                        @selection-change="handleSelectionChange">
                    <el-table-column
                            type="selection"
                            width="55">
                    </el-table-column>
                    <el-table-column
                            :label="$L('经验类型')"
                            align="left">
                        <div slot-scope="scope" class="status">
                            <template v-if="scope.row.typeFlag == 1">
                                {{ $L('消费') }}<br>
                            </template>
                            <template v-else-if="scope.row.typeFlag == 2">
                                {{ $L('赠送经验值') }}<br>
                            </template>
                            <span class="font-grey">{{$L('订单号')}}: {{ scope.row.orderSn }}</span>
                        </div>
                    </el-table-column>
                    <el-table-column
                            prop="points"
                            :label="$L('经验值')"
                            align="center">
                        <div slot-scope="scope" class="font-green">
                            {{ scope.row.points }}
                        </div>
                    </el-table-column>
                    <el-table-column
                            prop="time"
                            :label="$L('时间')"
                            align="center">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            :label="$L('描述')"
                            align="left">
                        <div slot-scope="scope" >
                            <template v-if="scope.row.typeFlag == 1">
                                {{ $L(' 消费')}}{{ scope.row.orderAmount }}{{$L(' 赠送')}}{{scope.row.points}}{{$L(' 个经验值') }}
                            </template>
                            <template v-else-if="scope.row.typeFlag == 3">
                                {{ $L('管理员手动变动会员分组') }}
                            </template>
                            <template v-else-if="scope.row.typeFlag == 2">
                                {{ $L('初始化增加经验积分')}}{{ scope.row.points }}
                            </template>
                        </div>
                    </el-table-column>
                </el-table>
                <!-- 分页 -->
                <div class="list-pages" v-if="tableData.allNum>0">
                    <el-pagination
                            background
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-size="pageSize"
                            layout="prev, pager, next,jumper"
                            :total="tableData.allNum">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import  sectionTitle from "../../../components/common/sectionTitle.vue"
    import userCard from "../../../components/coupon/userCard.vue"
    export default {
        name: "card",
        components:{
            userCard,sectionTitle
        },
        inject: ['reload'],
        data () {
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                isCreateVip:0,
                isHasCard:'',
                getCard:'',
                openCard: 0,
                info:{},
                getcardForm:{},
                telPhone:'',
                authCode:0,
                code:'',
                disabled:true,
                privilege:{},
                tableData:{},
                SearchTimeZone:'',
                dtCreateStart:'',
                dtCreateEnd:'',
                pageSize:10,
                currentPage:1,
                msgArr:[],
                phoneNumber:'',
                phoneCode:'',
                show: true,
                count: '',
                timer: null,
                currencyCode:''
            }
        },
        created(){
            if (this.$isMobile()) {
                this.$router.push({name:'userCardMobile'});
            } else {
                this.getData();
            }

        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            formListLang(val){
                let x;
                switch(val){
                    case '姓名':
                        x = this.$L(' 姓名');
                        break;
                    case '性别':
                        x = this.$L('性别');
                        break;
                    case '生日':
                        x = this.$L('生日');
                        break;
                    case '邮箱':
                        x = this.$L('邮箱');
                        break;
                    case '详细地址':
                        x = this.$L('详细地址');
                        break;
                    case '学历':
                        x = this.$L('学历');
                        break;
                    case '行业':
                        x = this.$L('行业');
                        break;
                    case '收入':
                        x = this.$L('收入');
                        break;
                    case '爱好':
                        x = this.$L('爱好');
                        break;
                    case '手机号':
                        x = this.$L('手机号');
                        break;
                    default:
                        x = this.$L('未知');
                        break;
                }
                return x;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    type: 2,
                    p: _self.currentPage,
                    dtCreateStart: _self.dtCreateStart,
                    dtCreateEnd: _self.dtCreateEnd,
                };
                _self.$api('getcardInfo', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.info = reData.data.info;
                        _self.privilege = reData.data.info.privilege;
                        _self.isCreateVip = reData.data.isCreateVip;
                        _self.isHasCard = reData.data.isHasCard;
                        _self.getCard = reData.data.getCard;
                        _self.openCard = reData.data.openCard;
                        _self.priceGap = reData.data.priceGap;
                        _self.getcardForm = reData.data.form;
                        _self.tableData = reData.data.experList;
                        _self.currencyCode = reData.data.currencyCode;
                        _self.loading = false;
                        _self.authCode = reData.data.code;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            backFunc() {
                this.$router.push({name: 'userCard'});
            },
            submitFunc() {
                const _self = this;
                let param = [];
                for(let i in _self.getcardForm){
                    param[_self.getcardForm[i]['fieldname']] = _self.info[i] != undefined ? _self.info[i] : "";
                }
                if (param.user_form_info_flag_mobile === '' || param.user_form_info_flag_mobile == undefined){
                    _self.$message({
                        type: 'info',
                        message: _self.$L('请输入手机号!')
                    });
                    return ;
                }

                if (_self.authCode == 1) {
                    if (!this.code.trim()) {
                        _self.$message({
                            type: 'info',
                            message: _self.$L('请输入验证码!')
                        });
                        return ;
                    }
                }

                let arr={
                    type: 'active_user',
                    param: param,
                    code:this.code,
                };
                _self.$api('activeCard', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.$message({
                            type: 'success',
                            message: _self.$L('成功领取会员卡!')
                        });
                        _self.getData();
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.backFunc();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            if (action === 'cancel') {
                                _self.$router.push({name: 'home'});
                            } else {
                                _self.loading = false;
                                _self.showError(_self.$L('网络请求失败'),_self.$L('请刷新页面重试'),'error');
                            }
                        });
                    }
                }, function (error) {
                    _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                        confirmButtonText: _self.$L('重新登录'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                    }).catch(action => {
                        _self.backFunc();
                    });
                });

            },
            getCodeFunc(){
                console.log("item", this.info);
                let param = [];
                for(let i in this.getcardForm){
                    param[this.getcardForm[i]['fieldname']] = this.info[i];
                }
                if (param.user_form_info_flag_mobile === '' || param.user_form_info_flag_mobile == undefined){
                    this.$message({
                        type: 'info',
                        message: this.$L('请输入手机号!')
                    });
                }
                if (param.user_form_info_flag_mobile !== '') {
                    let reg = /^1[0-9]{10}$/;
                    if (!reg.test(param.user_form_info_flag_mobile)) {
                        this.$message.error(this.$L("手机号码不正确"));
                        return;
                    }
                }
                const _self = this;
                _self.loading = true;
                let arr = {
                    send_mobile: 1,
                    mobile: param.user_form_info_flag_mobile,
                };
                _self.$api('sendCode', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;

                        const TIME_COUNT = _self.tableData.limitTime;

                        if (!_self.timer) {
                            _self.count = TIME_COUNT;
                            _self.show = false;
                            _self.timer = setInterval(() => {
                                if (_self.count > 0 && _self.count <= TIME_COUNT) {
                                    _self.count--;
                                } else {
                                    _self.show = true;
                                    clearInterval(_self.timer);
                                    _self.timer = null;
                                }
                            }, 1000)
                        }

                        console.log("false:", _self.show);

                        _self.$message({
                            message: _self.$L('新手机验证码发送成功咯！'),
                            type: 'success'
                        })

                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                            _self.$emit("closeDialog");
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            if (action == 'cancel') {
                                _self.$gotoWebHome();
                            } else {
                                _self.reload();
                            }
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                        _self.$emit("closeDialog");
                    });
                });

            },
            handleSelectionChange(val) {
                let arr = [];
                for (let i in val) {
                    arr[i] = val[i].id;
                }
                this.msgArr = arr;
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val){
                this.pageSize = val;
                // this.getData();
            },
            changeTypeFunc(val){
                // this.type = val;
                console.log(val);
            },
            delMsgFunc() {
                const _self = this;
                if ((Array.isArray(this.msgArr) && this.msgArr.length === 0)) {

                    //未知错误
                    _self.$confirm(_self.$L('您还没有选中经验记录，请勾选后再做此操作'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'warning',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        if (action === 'cancel') {
                            _self.$router.push({name: 'home'});
                        } else {
                            _self.loading = false;
                            _self.showError(_self.$L('网络请求失败'),_self.$L('请刷新页面重试'),'error');
                        }
                    });
                    return;
                }

                let logIds;

                    logIds = this.msgArr;

                let arr = {
                    logIds: logIds
                }
                _self.$confirm(_self.$L('此操作将永久删除该经验记录, 是否继续?'), _self.$L('提示'), {
                    confirmButtonText: _self.$L('确定'),
                    cancelButtonText: _self.$L('取消'),
                    type: 'warning'
                }).then(() => {
                    _self.$api('deleteExperienceList', arr, function (reData) {
                        if (reData.flag === 1) {
                            _self.loading = false;
                            _self.$message({
                                type: 'success',
                                message: _self.$L('删除成功!')
                            });
                            _self.getData();
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            console.log(reData);
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.backFunc();
                            });
                        }
                    }, function (error) {
                        console.log(error);
                        //网络请求错误
                        _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), { //todo:msg还未更换语言包语言包
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.backFunc();
                        });
                    });
                }).catch(() => {

                });
            },
            SearchTimeFunc(time){
                if(time !== null){
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                }else{
                    this.dtCreateStart = '';
                    this.dtCreateEnd = '';
                }
                this.getData();
            },
            timeFunc(time){
                console.log(time);
            }

        }
    }
</script>

